@import 'normalize';
@import 'bootstrap';

$slate: #cdd7d6;
$navy: #102542;
$peach: #f87060;

$obsidian: "Obsidian A", "Obsidian B", 'Helvetica', Arial, sans-serif;
$serif: "Surveyor SSm A", "Surveyor SSm B", Georgia, "Times New Roman", serif;

@mixin typesmooth() {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering:optimizeLegibility;
}

@mixin border-radius($border-radius) {
  -moz-border-radius: $border-radius;
    -webkit-border-radius: $border-radius;
    border-radius: $border-radius;
}

@mixin transition($value) {
  -webkit-transition: $value; /* Safari */
    transition: $value;
}

@mixin transform($value) {
  -webkit-transform: $value; /* Safari */
    transform: $value;
}

@mixin box-shadow($value) {
  -webkit-box-shadow: $value;
  -moz-box-shadow: $value;
  box-shadow: $value;
}

html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	font-family: $serif;
	@include typesmooth();
	background-color: $navy;
}

@import 'header';
@import 'typography';

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

.navy {
	background-color: $navy;
}

.grey {
	background-color: #efefef;
}

%full-height {
	height: 100vh;
	min-height: 400px;
	width: 100%;
	text-align: center;
	position: relative;
	.center-content {
		position: absolute;
		width: 100%;
		left: 0;
		top: 50%;
		@include transform(translateY(-50%));
	}
}

.full-height {
	@extend %full-height;
}

.parallax-container {
  overflow: hidden;
  transform: translateZ(0);
  .layer {
    z-index: -1;
  }
}

.emblem {
	position: fixed;
	bottom: 1rem;
	right: 1rem;
	z-index: 100;
	width: 2rem;
	height: auto;
}

.hero {
	@extend %full-height;
	height: 150vh;
	h1 {
		font-size: 3rem;
		font-size: 16vw;
		@media (min-width: 500px) {
			font-size: 7.5rem;
			font-size: 10vw;
		}
		margin-bottom: 1rem;
	}
	h2 {
		font-size: 0.85rem;
		@media (min-width: 500px) {
			font-size: 1rem;
		}
	}
	.center-content {
		top: 30%;
		@include transform(translateY(-30%));
	}
}

#intro {
	position: relative;
	.center-content {
		top: 50%;
		@include transform(translateY(-50%));
		p {
			background-color: rgba(16, 37, 66, 0.8);
			padding: 1rem;
		}
	}
	&:before {
	    content: '';
	    position: absolute;
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    background-image: linear-gradient(rgba(16,37,66,1), rgba(16,37,66,0.85), rgba(16,37,66,0), rgba(16,37,66,0));
	}
}

.lines-container {
	background-image: url('../img/line.svg');
	background-repeat: repeat;
	background-size: 20px 20px;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}


#page-content {
	section {
		padding: 6rem 0;
	}
}
#previous-events {
	position: relative;
	@include box-shadow(0px -5px 8px rgba(0,0,0,0.5));
	h2 {
		font-family: "Fell Type A", "Fell Type B", serif;
	}
	@media(min-width: 768px) {
		h1 {
			margin-left: 20px;
		}
		blockquote {
			margin-left: -40px;
		}
	}
	@media(min-width: 992px) {
		blockquote {
			margin-left: 15px;
		}
	}
	.event-images {
		z-index: -1;
		img {
			width: 80%;
			height: auto;
			@include box-shadow(0px 0px 6px rgba(0,0,0,0.5));
		}
		.small-image {
			z-index: 2;
			width: 30%;
			height: auto;
			position: absolute;
			right: 1rem;
			top: 50%;
			@include transform(translateY(-50%));
		}
	}
	.float-right {
		@media (min-width: 992px) {
			text-align: right;
			margin-top: 1rem;
			a.button {
				margin-right: -4rem;
			}
		}
	}
}

#other {
	@include box-shadow(inset 0px 5px 8px rgba(0,0,0,0.5));
	section {
		padding: 1rem 0;
	}
}