h1 {
	color: $peach;
	font-family: $obsidian;
	font-style: normal;
	font-weight: 400;
	line-height: 1;
	font-size: 3rem;
	@media (min-width: 500px) {
		font-size: 3rem;
	}
	@include typesmooth();
	margin-bottom: 3rem;
}

h2 {
	color: #b3a395;
	font-family: $serif;
	font-style: normal;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 1rem;
	@include typesmooth();
}

h3 {
	font-family: $serif;
	font-style: normal;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 0.875rem;
	@include typesmooth();
	margin-top: 2rem;
}

.grey {
	color: #333333;
	
	h1 {
		color: $navy;
	}
	h2 {
		color: #777777;
	}
}

.navy {
	color: white;
	h1 {
		color: $peach;
	}
	h2 {
		color: #b3a395;
	}
	a {
		color: $peach;
	}
}


a.button, button, .button {
	background-color: $peach;
	@include box-shadow(0px 0px 8px rgba(0,0,0,0.2));
	border: 1px solid $navy;
	padding: 0.5rem 1rem;
	color: white;
	@include transition(background-color 500ms);
	&:hover {
		background-color: #ff343e;
		text-decoration: none;
	}
}

blockquote {
	font-family: "Fell Type A", "Fell Type B", serif;
	font-size: 2rem;
	font-weight: 400;
	line-height: 1.2;
	margin-top: 2rem;
}

::-moz-selection {
	background: black;
	color: $peach;
}

::selection {
	background: black;
	color: $peach;
}